<template>
	<view class="container">
		<view class="bg-top">
			<view class="gps-title">
				<text class="title">奥园商管官方招商平台</text>
				<view class="position" @click="changeCity">
					<image mode="widthFix"  class="gps-icon" src="../../static/img/index/ic_map.png" ></image>
					<text>{{location}}</text> 
				</view>
			</view>
		</view>
		<view class="menu-tab">
			<view class="item-tab" @click="projectGo">
				<image class="tab-icon" src="../../static/img/index/icon/ic_business.png" mode="widthFix"></image>
				<text class="tab-desc">商业项目</text>
			</view>
			<view class="item-tab" @click="applicationGo">
				<image class="tab-icon" src="../../static/img/index/icon/ic_join_in.png" mode="widthFix"></image>
				<text class="tab-desc">我要入驻</text>
			</view>
		</view>
		
		<view class="box">
			<view class="box-title">
				<text class="title-left">推荐项目</text>
				<view class="more" @click="projectGo">
					<text class="more-desc">查看更多</text>
					<image class="more-icon" src="../../static/img/index/ic_back.png" mode="widthFix"></image>
				</view>
			</view>
			<view class="list-item">
				<view class="xm-item" v-for="(item,index) in listData" @click="detailGo(item.id)">
					<image class="xm-bg" :src="item.img" ></image>
					<view class="xm-contet">
						<view class="xm-title">{{item.name}}</view>
						<view class="xm-desc">{{item.address}}</view>
						<view class="xm-desc">{{item.area}}万㎡|{{item.open_time}}</view>
					</view>
					<view class="status">
						<text class="status-desc open"  v-if="item.status==1">已开业</text>
						<text class="status-desc ready" v-if="item.status==2">筹备中</text>
						<text class="status-desc close" v-if="item.status==3">已关闭</text>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import api from '../../api/api.js'
	import QQMap from '../../static/js/qqmap-wx-jssdk.js'
	import local from '../../common/local.js'
	export default {
		data() {
			return {
				location:'定位...',
				listData:[],
				getParams:{
					page:'1',
					limit:'50',
					city:'',
				}
			}
		},
		onLoad(option) {
			let maid='A01';
			local.localStorage.set('maid',maid);
			wx.showShareMenu({
				withShareTicket:true,
				//设置下方的Menus菜单，才能够让发送给朋友与分享到朋友圈两个按钮可以点击
				menus:["shareAppMessage","shareTimeline"]
			})
		},
		//发送给朋友
		onShareAppMessage(res) {
			// 此处的distSource为分享者的部分信息，需要传递给其他人
			return {
				title: '欢迎使用奥园招商小程序', // 分享名称
				path: 'pages/index/index?project_id=A01', // 这里写你这个页面的路径
				success: function(shareTickets) {
					console.log(shareTickets + '成功');
					// 转发成功
				},
				fail: function(res) {
					console.log(res + '失败');
					// 转发失败
				},
				complete: function(res) {
					// 不管成功失败都会执行
				}
			}
		},
		//分享到朋友圈
		onShareTimeline(res) {
				return {
					title: '欢迎使用奥园招商小程序',
					type: 0,
					query: 'maid=A01'
				}
		},
		onShow() {
			let localtion=JSON.parse(local.localStorage.get('localtion'));
			console.log(localtion)
			if(localtion&&localtion.isChange){
				this.location=localtion.city;
				this.getParams.city=localtion.city;
				 this.initData()
			}
			 
		},
		mounted() {
			this.getAuthorizeInfo();
		},
		methods: {
			initData(){
				 let city=this.getParams.city.replace('市','');
				 this.getParams.city=city;
				api.getProjectList(this.getParams).then(res=>{
					console.log(res)
					if(res.code===0){
						this.listData=res.data.list;
					}
				})
			},
			changeCity(){
				uni.navigateTo({
					url:'../address/address'
				})
			},
			detailGo(id){
				uni.navigateTo({
					url:'../projectDetail/projectDetail?project_id='+id
				})
			},
			applicationGo(){
				let loginBool=local.localStorage.get('phoneInfo'); 
				if (loginBool== undefined || loginBool== null) {
					
					uni.showToast({
						title: "您还未登录!",
						icon: "none",
						position: 'bottom'
					})
					setTimeout(()=>{
						console.log(555)
						uni.navigateTo({
							url:'../login/authorize'
						})
					},1500)
				}else{
					uni.navigateTo({
						url:"../application/application"
					})
				}
			},
			projectGo(){
				uni.navigateTo({
					url:"../projectList/projectList"
				})
			},
			getAuthorizeInfo(){
				const that = this;
				uni.authorize({
					scope: 'scope.userLocation',
					success() { // 允许授权
						that.getLocationInfo();
					},
					fail(){    // 拒绝授权
						that.openConfirm();
						console.log("你拒绝了授权，无法获得周边信息")
					}
				})
			},
			// 获取地理位置
			getLocationInfo(){ 
				let that=this;
				uni.getLocation({
					type: 'wgs84',
					success (res) {
						let qqmapsdk = new QQMap({
						  key: '5TPBZ-ZFB3R-NCZWD-WMSLS-QPZ6S-VHFVD'
						});
						qqmapsdk.reverseGeocoder({
							location:{
								latitude: res.latitude,
								longitude: res.longitude
							},
							success(resd){
								that.location=resd.result.ad_info.city
								local.localStorage.set('localtion',JSON.stringify({city:resd.result.ad_info.city}))
								that.getParams.city=resd.result.ad_info.city
								that.initData();
							}
						})
					}
				});
			},
	
			// 再次获取授权
			// 当用户第一次拒绝后再次请求授权
			openConfirm(){
				uni.showModal({
					title: '请求授权当前位置',
					content: '需要获取您的地理位置，请确认授权',
					success: (res)=> {
						if (res.confirm) {
							uni.openSetting();// 打开地图权限设置
						} else if (res.cancel) {
							uni.showToast({
								title: '你拒绝了授权，无法获得周边信息',
								icon: 'none',
								duration: 1000
							})
						}
					}
				});
			}
		}
	}
</script>

<style lang="scss" scoped>
	.container {
		font-size: 14px;
		line-height: 24px;
		font-family: PingFangSC-Medium, PingFang SC;
	}
	.bg-top{
		height: 270rpx;
		background: url('data:image/png;base64,') bottom no-repeat , 
		            linear-gradient(to bottom, #0191D7 , #54C2F0);
		background-size: 88% 100%;
		position: relative;
	}
	.title{
		
	}
	.gps-title{
		width: 72%;
		padding: 26rpx 26rpx;
		display: flex;
		justify-content: space-between;
		align-items: center;
		background-color: #fff;
		position: absolute;
		bottom: -46rpx;
		left: 50%;
		margin-left: -294rpx;
		font-size: 36rpx;
		box-shadow: 0px 1px 8px 0px rgba(1, 145, 215, 0.3);
		border-radius: 8rpx;
		color: #0191D7;
		font-weight: 500;

	}
	.position{
		display: flex;
		align-items: center;
		color: #333;
	}
	.position .gps-icon{
		width: 24rpx;
		margin-right: 18rpx;
	}
	.menu-tab{
		width: 50%;
		margin: 0 auto;
		padding: 80rpx 0 0;
		display: flex;
		justify-content: space-between;
	}
	
	.item-tab{
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	.item-tab .tab-icon{
		width: 80rpx;
		height: 80rpx;
		margin-bottom: 12rpx;
	}
	.box{
		padding: 30rpx 40rpx 0;
		.box-title{
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin-bottom: 12rpx;
			.title-left{
				font-size: 30rpx;
			}
			.more{
				display: flex;
				align-items: center;
				color: #999;
				.more-desc{
					margin-right: 10rpx;
				}
				.more-icon{
					width: 14rpx;
				}
			}
		}
		.list-item{
			.xm-item{
				// margin-bottom: 30rpx;
				display: flex;
				padding: 20rpx 0;
				border-bottom: 1rpx solid #eee;
				.xm-bg{
					width: 30%;
					height: 144rpx;
					margin-right: 3%;
					border-radius: 8rpx;
				}
				.xm-contet{
					width: 53%;
					.xm-title{
						font-size: 30rpx;
						text-overflow:ellipsis; 
						white-space: nowrap;
						overflow: hidden;
					}
					.xm-desc{
						color: #999;
						font-size: 22rpx;
						text-overflow:ellipsis; 
						white-space: nowrap;
						overflow: hidden;
					}
				}
				.status{
					flex: auto;
					color: #fff;
					
					.status-desc{
						
						padding: 2rpx 8rpx;
						font-size: 22rpx;
						border-radius: 8rpx;
						&.open{
							background-color: #0191D7;
						}
						&.ready{
							background-color: #00C26C;
						}
						&.close{
							background-color: #CCCCCC;
						}
					}
				}
			}
		}
	}
	
			
	
</style>
